body {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(135deg, #f0f4f8, #d9e2ec);
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

div {
  background: white;
  padding: 30px;
  border-radius: 10px;
  max-width: 800px;
  text-align: center;
}

h1,
.h1 {
  font-size: 2rem;
  color: #333;
  margin-bottom: 1.5rem;
  font-weight: 700;
}

button {
  padding: 12px 25px;
  box-shadow: 0px 0px 5px black;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 8px;
  margin: 10px;
  height: 70px;
  min-width: 150px;
}
button :hover {
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}
